import React, { useRef, useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  Link,
  Redirect,
  useRouteMatch,
  useParams,
  useHistory
} from "react-router-dom";
import { config as AmapReactConfig, Amap, Marker } from '@amap/amap-react';

import './Home.css';

// import GaoDeMap from './GaoDeMap'
import ScenicCard from './ScenicCard'
import Empty from './Empty'
import Scenic from './Scenic'

import markersData from './markers'

AmapReactConfig.version = '2.0'; // 默认2.0，这里可以不修改
AmapReactConfig.key = 'e7711f1da56c12d72258e70bcff633b6';
AmapReactConfig.plugins = [
//   'AMap.ToolBar',
  'AMap.MoveAnimation',
//   // 在此配置你需要预加载的插件，如果不配置，在使用到的时候会自动异步加载
];

const allScenic = markersData.filter(markerInfo => {
  return markerInfo.type === 'scenic';
});



function renderMarkers(scenicId, type, markerId, updateMarkers, originData, setCenter, setZoom) {
  let scenicMarker = originData.filter(markerInfo => {
    return markerInfo.id === scenicId
  });

  if (scenicMarker.length) { // 获取到景区信息了
    if (type && type !== '000') {
      let typeMarkers = originData.filter(markerInfo => {
        return markerInfo.type === type && markerInfo.scenic === scenicId;
      })
      updateMarkers(typeMarkers);
    } else {
      let typeMarkers = originData.filter(markerInfo => {
        return markerInfo.scenic === scenicId;
      })
      updateMarkers(typeMarkers);
    }
    setCenter(scenicMarker[0].position);
    setZoom(scenicMarker[0].zoom);
  } else { // scenicId为000、空、未匹配，展示全部景点的视图
    let typeMarkers = originData.filter(markerInfo => {
      return markerInfo.type === type
    })

    if (typeMarkers.length) { // 渲染指定type的marker
      updateMarkers(typeMarkers);
    } else { // 渲染全部景区
      let scenicData = originData.filter(m => m.type === 'scenic');
      updateMarkers(scenicData);
    }
    setCenter([118.86765,28.965282])
    setZoom(14);
  }

  if (markerId) {
    let marker = originData.find(markerInfo => {
      return markerInfo.id === markerId;
    })
    if (marker) {
      setCenter(marker.position);
    }
  }


  // if (!scenicId || scenicId === '000') {
  //   updateMarkers(allScenic);
  //   setZoom(14);
  // }
  // else {
  //   let res = originData.filter(markerInfo => {
  //     return markerInfo.id === scenicId
  //   });

  //   if (res.length) {
  //     // console.log(res)
  //     updateMarkers(res);
  //     setCenter(res[0].position);
  //     setZoom(res[0].zoom);
  //   } else {
  //     let res = originData.filter(markerInfo => {
  //       return markerInfo.type === 'scenic';
  //     });

  //     updateMarkers(res);
  //     setZoom(14);
  //   }
  // }
}
function Home({osType}) {
  const [scenicId, setScenicId] = useState('000'); // 默认展示所有景点
  const [type, setType] = useState('');
  const [markerId, setMarkerId] = useState('');
  const [secnicMenu, setScenicMenu] = useState(false);
  let match = useRouteMatch();
  console.log('Home - route match: ', JSON.stringify(match));

  const $map = useRef();
  const [center, setCenter] = useState([118.86765,28.965282]);
  const [zoom, setZoom] = useState(14);
  const [markers, setMarkers] = useState([]);

  const [isQRShow, setIsQRShow] = useState(false)

  let history = useHistory();

  const markHandler = (m) => {
    if (m.type !== 'scenic') {
      console.log(`renderMarkersByType marker clicked: `, m)
      // setCenter(m.position);
      history.push(`/${m.scenic}/${ scenicId === '000' ? '000' : (type ? type : '000')}/${m.id}`);
    } else {
      console.log(`renderScenics marker clicked: `, m)
      // setCenter(m.position);
      history.push(`/${m.id}`);
    }
  }
  const switchQR = () => {
    setIsQRShow(!isQRShow);
  }


  console.log('Home - scenicId: ', scenicId);

  useEffect(() => {
    console.log(`Home - !scenicId || scenicId === '000': `, !scenicId || scenicId === '000');
    renderMarkers(scenicId, type, markerId, setMarkers, markersData, setCenter, setZoom)

  }, [scenicId, type, markerId])

  // const {scenicId, type, markerId} = useParams()
  // console.log(`Home: scenic-${scenicId}, type-${type}, marker-${markerId}`)
  const toggleMenu = (e) => {
    setScenicMenu(!secnicMenu);
    e.preventDefault();
    e.stopPropagation();
    console.log('You clicked submit.');
  }

  return (
    <div className="Home" onClick={() => setScenicMenu(false)}>
      <Amap
        ref={$map}
        zoom={zoom}
        center={center}
        features={['bg', 'building', 'road', 'point']}
        onComplete={() => console.log('map complete!')}
      >
        {
          markers.map(marker => <Marker
            key={marker.id}
            position={marker.position}
            animation="AMAP_ANIMATION_DROP"
            onClick={() => markHandler(marker)}
            clickable={true} >
            <div className={`marker ${marker.type} ${marker.id === markerId ? 'active' : ''}`}></div>
          </Marker>)
        }
      </Amap>

      {/* <GaoDeMap scenicId={scenicId} type={type} markerId={markerId} /> */}
      {/* <ScenicCard scenicId={scenicId} type={type} markerId={markerId} /> */}

      <Switch>
        {/* <Route path={`${match.path}:scenicId?/:type?/:markerId?`}> */}
        <Route path={`${match.path}:scenicId`}>
          <Scenic
              osType={osType}
              type={type}
              markerId={markerId}
              setScenicId={setScenicId}
              setType={setType}
              setMarkerId={setMarkerId}
            />
        </Route>
        {/* <Route path={`${match.path}`}>
          <div>oops!</div>
        </Route> */}
      </Switch>

      <div className="left-filter-bar">
        <NavLink to={`${match.url}000`} className="filter-handle" activeClassName="selected" exact>
          <span className="scenic" />
        </NavLink>
        {/* <div className="filter-handle selected">111</div> */}
        <NavLink to={`${match.url}000/parking`} className="filter-handle" activeClassName="selected">
          <span className="parking" />
        </NavLink>
        <NavLink to={`${match.url}000/toilet`} className="filter-handle" activeClassName="selected">
          <span className="toilet" />
        </NavLink>
      </div>

      <div className="main-nav">
        <div className="nav-button scenic-change selected">
          <span onClick={toggleMenu}>其它景点</span>
        </div>
        <Link to={`${match.url}000`} className="nav-button main">
          <span className="parking" />
        </Link>
        <div className="nav-button follow-wechat">
          <span onClick={switchQR}>关注公众号</span>
        </div>
      </div>

      {
        secnicMenu && (
          <div className="scenic-list">
            <ul>
              <li><Link to={`${match.url}001`} >水亭门历史文化街区</Link></li>
              <li><Link to={`${match.url}002`} >北门街</Link></li>
              <li><Link to={`${match.url}003`} >衢州博物馆</Link></li>
              <li><Link to={`${match.url}004`} >孔氏南宗家庙</Link></li>
              <li><Link to={`${match.url}005`} >中国儒学馆</Link></li>
              <li><Link to={`${match.url}006`} >孔子文化园</Link></li>
              <li><Link to={`${match.url}007`} >严家淤岛</Link></li>
              <li><Link to={`${match.url}008`} >鹿鸣半岛时尚文化创业园</Link></li>
              <li><Link to={`${match.url}009`} >智慧新城草坪公园</Link></li>
              <li><Link to={`${match.url}010`} >鹿鸣山文化院街</Link></li>
              <li><Link to={`${match.url}011`} >鹿鸣山</Link></li>
            </ul>
          </div>
        )
      }

      <div className="jixiangwu"></div>
      {
        isQRShow && (
          <div className="gongzonghaoQR">
            <img src="/gongzonghaoQR.jpeg" />
            <span className="closeQR" onClick={switchQR}>
              <span></span>
            </span>
          </div>
        )
      }
    </div>
  );
}

export default Home;
