import React, { useState, useEffect } from "react";
// import Excel from "exceljs";
import exportExcel from "./exportExcel";

import './ExportExcelComp.css';


function App() {
  const [json, setJson] = useState('')
  const [title, setTitle] = useState('功能反馈表')

  const handleChange = (e) => {
    setJson(e.target.value)
    e.preventDefault()
  }
  const handleTitleChange = (e) => {
    setTitle(e.target.value)
    e.preventDefault()
  }

  const handleClick = () => {
    // console.log('json', json)
    if (!json) {
      alert('请输入数据')
      return
    }
    const tableData = JSON.parse(json)
    exportExcel(tableData, title)
  }

  return (
    <div className="App">
      <textarea placeholder="请输入需要转成excel的数据" style={{width: "100%", height: 300}}  onChange={handleChange} />
      <p>
      <label htmlFor="fname">表名：</label>
        <input id="fname" style={{width: 200, height: 32}} placeholder={title}  onChange={handleTitleChange} />
      </p>
      <div onClick={handleClick} style={{ display:"inline-block", cursor: "pointer", padding: '10px 30px', margin: 10, border: "1px solid red" }}>导出 Excel</div>
    </div>
  )
}
export default App;
