import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import './App.css';
import Home from './Home'
import Empty from './Empty'
import ExportExcelComp from './ExportExcelComp'

function judgeClient () {
  let u = navigator.userAgent;
  let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;   //判断是否是 android终端
  let isIOS = /(iPhone|iPad|iPod|iOS)/i.test(u);     //判断是否是 iOS终端
  const isWin = u.indexOf('Window') > 0;
  const isMac = u.indexOf('Mac OS X') > 0;
  const isLinux = u.indexOf('Linux') > 0;
  // const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端

  if (isAndroid) {
    return 'Android';
  } else if (isIOS) {
    return 'IOS';
  } else {
    return 'PC';
  }
}

function App() {
  console.log('app');
  const osType = judgeClient()

  return (
    <Router>
      <div className="App">

        <Switch>
          <Route path="/test">
            <Empty />
            <div>1111</div>
          </Route>
          <Route path="/exportexcel">
            <ExportExcelComp />
          </Route>
          {/* <Route path={`/:scenicId?/:type?/:markerId?`} > */}
          <Route path="/" >
            <Home osType={osType} />
          </Route>
        </Switch>
    </div>
    </Router>
  );
}

export default App;
