import Excel from "exceljs";

function loadImgs(urls) {
  let proList = []

  for (let i = 0; i < urls.length; i++) {
    const pro = new Promise(resolve => {
      let img = new Image();
      img.crossOrigin = '*'
      img.src = urls[i]
      img.onload = () => {
        resolve(img)
      }
    })

    proList.push(pro)
  }

  return Promise.all(proList).then((rs) => {
    // console.log('finish all', urls)
    return Promise.resolve(rs)
  })
}

async function getBase64Img(urls) {
  const imgs = await loadImgs(urls)
  // console.log(imgs)

  let width = 0, height = 0
  for (let i = 0; i < imgs.length; i++) {
    const img = imgs[i];
    // console.log(img)
    width += (img.naturalWidth / 2 + 10)
    if (height < img.naturalHeight / 2)  height = img.naturalHeight / 2
  }
  const canvas = document.createElement('canvas')
  canvas.width = width + 10
  canvas.height = height + 20
  const ctx = canvas.getContext('2d')
  let left = 10
  for (let j = 0; j < imgs.length; j++) {
    const img = imgs[j];

    ctx.drawImage(img, left, 10, img.naturalWidth / 2, img.naturalHeight / 2)
    left += (img.naturalWidth / 2 + 10)
  }

  return ({
    img: canvas.toDataURL('image/jpeg'),
    width: width / (height / 133),
    imgNum: imgs.length
  })
}

// 将二进制转为Excel并下载
const writeFile = (fileName, content) => {
  let a = document.createElement("a");
  let blob = new Blob([content], { type: "text/plain" });

  a.download = fileName;
  a.href = URL.createObjectURL(blob);

  a.click();
};

