// 给指定表添加表头
const addHeaderToSheet = (sheet, headers) => {
  let columns = []; // 表头
  // 表头格式化
  headers.map((item) => {
    columns.push({
      header: item.title,
      key: item.key || item.dataIndex,
      width: parseInt(item.width / 6, 10) || 80,
    });
    return true;
  });
  // 添加表头
  sheet.columns = columns;
}

// 给指定标添加数据
const addRowsToSheet = async (workbook, sheet, rows) => {
  sheet.addRows(rows);
  // 设置每一列样式 居中
  const row = sheet.getRow(1);
  row.eachCell((cell, rowNumber) => {
    sheet.getColumn(rowNumber).alignment = {
      vertical: "middle",
      horizontal: "center",
    };
  });


  const contentCol = sheet.getColumn('content')
  contentCol.eachCell((cell, rowNumber) => {
    if (rowNumber === 1) return;
    cell.alignment = {
      wrapText: true,
      horizontal: 'left',
      vertical: "middle"
     }
  })
  const imgCol = sheet.getColumn('img')

  // 遍历此图片列中的所有当前单元格
  const cells = []
  imgCol.eachCell(async function(cell, rowNumber) {
    cells.push(cell)
  });
  const cellNum = cells.length
  let colWidth = 0
  for (let i = 0; i < cellNum; i++) {
    const cell = cells[i];
    if (!(typeof cell.value === 'object' && cell.value.length > 0)) {
      if (cell.row !== 1) cell.value = ''
      continue
    }
    const row = sheet.getRow(cell.row);
    row.height = 110

    // 存在图片链接的继续
    const imgInfo = await getBase64Img(cell.value)
    if (colWidth < imgInfo.width) colWidth = imgInfo.width + 50
    // console.log(imgInfo)
    const imgId1 = workbook.addImage({
      base64: imgInfo.img,
      extension: 'jpeg',
    });
    // console.log(imgId1)
    sheet.addImage(imgId1, {
      tl: { col: cell.col - 1, row: cell.row - 1 },
      // br: { col: cell.col, row: cell.row + 1 },
      ext: {width: imgInfo.width, height: 133}
    });

    cell.value = ''
  }
  imgCol.width = colWidth / 6
}

const jsonToExcel = async (columns, tableRows) => {
  // 初始化 创建工作簿
  const workbook = new Excel.Workbook();
  // 设置工作簿属性
  workbook.creator = "汪飞";
  workbook.lastModifiedBy = "汪飞";
  workbook.created = new Date();
  workbook.modified = new Date();

  // 添加工作表
  let detailSheet = workbook.addWorksheet('明细', {views:[{state: 'frozen', ySplit:1}]});

  // 添加表头
  addHeaderToSheet(detailSheet, columns);

  // 添加表格数据
  await addRowsToSheet(workbook, detailSheet, tableRows);

  return workbook;
}
// 执行下载表格
const exportExcel = async (json, excelTitle) => {
  const columns = tableColumns(json)
  const tableRows = jsonToTableRows(json)
  const workbook = await jsonToExcel(columns, tableRows)
  // console.log(workbook)
  // 将表格数据转为二进制
  workbook.xlsx.writeBuffer().then((buffer) => {
    writeFile(`${excelTitle}.xlsx`, buffer);
  });
};

export default exportExcel
