import React, { useRef, useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  Link,
  useRouteMatch,
  useParams
} from "react-router-dom";


import './Home.css';

import ScenicCard from './ScenicCard'
import Empty from './Empty'



function Scenic({osType, type, markerId, setScenicId, setType, setMarkerId}) {
  let match = useRouteMatch();
  console.log('Scenic - match: ', match)
  const {scenicId} = useParams();
  // console.log(`Scenic: scenic-${scenicId}, type-${type}, marker-${markerId}`)

  // setType('');
  // setMarkerId('');
  useEffect(() => {
    setScenicId(scenicId);
    return () => {
      setScenicId('');
    }
  }, [scenicId]);


  return (
    <>
      <Switch>
        <Route path={`${match.path}/:type/:markerId?`}>
          <Empty scenicId={scenicId} setScenicId={setScenicId} setType={setType}  setMarkerId={setMarkerId}/>
        </Route>
      </Switch>

      <ScenicCard osType={osType} scenicId={scenicId} type={type} markerId={markerId} />
    </>
  );
}

export default Scenic;
