import React, { useRef, useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  Link,
  useRouteMatch,
  useParams
} from "react-router-dom";


function Empty({scenicId, setScenicId, setType, setMarkerId}) {
  let match = useRouteMatch();
  console.log('Empty', match)
  const {type, markerId} = useParams();
  console.log(`Empty: scenic-${scenicId}, type-${type}, marker-${markerId}`)

  useEffect(() => {
    console.log(`Empty useEffect: type-${type}, markerId-${markerId}`);
    setType(type);
    setMarkerId(markerId);
    return () => {
      setType('');
      setMarkerId('');
    }
  }, [type, markerId])


  return null;
}

export default Empty;
