import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
  Link,
  useRouteMatch,
  useParams,
  useHistory
} from "react-router-dom";

import './ScenicCard.css';

import markersData from './markers'

function ScenicCard({osType, scenicId, type, markerId}) {
  let match = useRouteMatch();
  let history = useHistory();
  console.log('ScenicCard', match)
  // const {scenicId, type, markerId} = useParams();
  let allMarkers = [];
  let total = 0;
  console.log(`ScenicCard: scenic-${scenicId}, type-${type}, marker-${markerId}`)

  const navClickHandle = (merchant) => {
    if (osType === 'Android') {
      //这个是安卓操作系统
      var d = new Date();
      window.location.href=`androidamap://navi?sourceApplication=南空圣地文化旅游区&poiname=${merchant.name}&lat=${merchant.position[1]}&lon=${merchant.position[0]}&dev=0`;
      //判断是否切出浏览器
      setTimeout(function(){
        let hidden = window.document.hidden || window.document.mozHidden || window.document.msHidden ||window.document.webkitHidden
        if(typeof hidden =="undefined" || hidden ==false){
          //应用宝下载地址
          window.location.href =`https://uri.amap.com/marker?position=${merchant.position.join(',')}&name=${merchant.name}`;
        }
      }, 2500);
    }
    if (osType === 'IOS') {
        //这个是ios操作系统
      var d = new Date();
      window.location.href=`iosamap://navi?sourceApplication=南空圣地文化旅游区&poiname=${merchant.name}&lat=${merchant.position[1]}&lon=${merchant.position[0]}&dev=0`;
      //判断是否切出浏览器
      setTimeout(function(){
        let hidden = window.document.hidden || window.document.mozHidden || window.document.msHidden ||window.document.webkitHidden
        if(typeof hidden =="undefined" || hidden ==false){
          //应用宝下载地址
          window.location.href =`https://uri.amap.com/marker?position=${merchant.position.join(',')}&name=${merchant.name}`;
        }
      }, 2500);
    }
    if (osType === 'PC') {
      window.location.href =`https://uri.amap.com/marker?position=${merchant.position.join(',')}&name=${merchant.name}`;
    }
  }
  const prevMarker = () => {
    let index = allMarkers.indexOf(markerId);
    if (index === 0) {
      index = allMarkers.length - 1;
    } else {
      index -= 1;
    }
    history.push(`/${scenicId}/${ scenicId === '000' ? '000' : (type ? type : '000')}/${allMarkers[index]}`);
  }
  const nextMarker = () => {
    let index = allMarkers.indexOf(markerId);
    if (index === allMarkers.length - 1) {
      index = 0;
    } else {
      index += 1;
    }
    history.push(`/${scenicId}/${ scenicId === '000' ? '000' : (type ? type : '000')}/${allMarkers[index]}`);
  }


  const scenic = markersData.find(marker => {
    return marker.id === scenicId;
  })
  if (!scenic) return null;

  const merchant = markersData.find(marker => {
    return marker.id === markerId
  }) || {intro: []}
  console.log(`ScenicCard marker: `, scenic, merchant)

  if (!type || type === '000') {
    markersData.filter(m => m.scenic === scenicId).forEach(m => {
      allMarkers.push(m.id);
    });
  } else {
    markersData.filter(m => m.scenic === scenicId && m.type === type).forEach(m => {
      allMarkers.push(m.id);
    });
  }
  total = allMarkers.length;


  {/* <div className={`scenic-card ${(markerId) ? 'bounceOutDown' : ''}`}> */}
  return (
    <div className={`scenic-card ${(markerId && (merchant.type === 'cate' || merchant.type === 'hotel')) ? 'show' : ''}`}>
      <div className="scenic-intro">
        <div className="scenic-avator" style={{backgroundImage: `url(${scenic.icon})`}}></div>
        <div className="scenic-desc">
          <h3 className="scenic-name">{scenic.name}</h3>
          <span className="scenic-address">{scenic.address}</span>
        </div>
        <div className="scenic-services-total">{`${total}家`}</div>
      </div>
      <div className="scenic-services">
        <div className="button-wrap">
          <NavLink to={`${match.url}/cate`} className="btn" activeClassName="selected">附近美食</NavLink>
        </div>
        <div className="button-wrap">
          <NavLink to={`${match.url}/hotel`} className="btn" activeClassName="selected">酒店住宿</NavLink>
        </div>
        <div className="button-wrap">
          <NavLink to={`${match.url}`} className="btn" activeClassName="selected">景区服务</NavLink>
        </div>
        {/* <div className="button-wrap">
          <span className="btn">景区服务</span>
        </div> */}
      </div>
      <div className="scenic-services-list">
        <div className="prev-service" onClick={prevMarker}></div>
        <div className="service-intro">
          <div className="service-intro-img">
            <img src={merchant.img} />
            <span>商户介绍</span>
          </div>
          <div className="service-intro-desc">
            <p>
              <span className="triangle"></span>
              <span>{merchant.address}</span>
            </p>
            {
              merchant.intro.map((text, i) => {
                return <p key={i}>
                  <span className="triangle"></span>
                  <span>{text}</span>
                </p>
              })
            }
          </div>
          <div className="service-intro-pics">
            <img src={merchant.pic} />
            <span className="navigation" onClick={() => navClickHandle(merchant)}>
              <span>去这里</span>
            </span>
          </div>
        </div>
        <div className="next-service" onClick={nextMarker}></div>
      </div>
    </div>
  );
}

export default ScenicCard;
